function split(source, start) {
  let match
  const buf = []
  const reg = /'[^']+'|"[^"]+"|[^\s<>\/=]+=?|(\/?\s*>|<)/g
  reg.lastIndex = start
  reg.exec(source)// skip <
  while (match = reg.exec(source)) {
    buf.push(match)
    if (match[1]) return buf
  }
}

exports.XMLReader = XMLReader
